.landing-div {
  min-height: 100%;
  overflow: hidden;
}

.header-color, .landing-grid {
  background: #1a2a6c;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #fdbb2d, #b21f1f, #1a2a6c);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #fdbb2d, #b21f1f, #1a2a6c); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  position: fixed;
}

.avatar-img {
  margin-top: 7%;
  margin-left: 1%;
  width: 17%;
  border-radius:50%;
}


.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  margin-bottom: 10%;
}

.banner-text  h1{
  font-size: 3.5em;
  font-weight: bold;
  color:white;
  padding: 1%;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
  padding-bottom: 2%;
}

.social-links i {
  color: white;
  font-size: 4em;
}

@media screen and (max-width: 680px) {

  .landing-grid {
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    position:absolute;

  }
  .avatar-img {
    width: 35%;
  }

  .social-links i {
    font-size: 1.5em;
    padding: 4px;
  }

  .banner-text  h1{
    font-size: 30px;
    padding: 10%;
  }

  .category-tabs {
    margin-top: 12%;
  }
}

.projects-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content:  flex-start;
}

.projects-card {
  margin-right: 1%;
  margin-top: 1%;
}
@media screen and (max-width: 680px) {
  .projects-grid  {
      overflow-x: auto;
  }
}

/* Button style     */
/* effect-underline */
.effect-underline {
  display: inline-block;
  position: relative;
}

div.effect-underline:after {
  align: center;
  text-align: center;
  content: '';
  position: absolute;
  left: 0;
  display: inline-block;
  height: 12px;
  width: 100%;
  border-bottom: 1px solid;
  margin-top: 30px;
  opacity: 0;
	-webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
	transition: opacity 0.35s, transform 0.35s;
	-webkit-transform: scale(0,1);
	transform: scale(0,1);
}

div.effect-underline:hover:after {
  opacity: 1;
	-webkit-transform: scale(1);
	transform: scale(1);
}

/* Contact Page */

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
  overflow: hidden;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: white;
  color: black;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}


.contact-grid h2  {
   font-family: 'Arial', sans-serif;
  font-size: 2em;
  font-weight: bold;
}
 .contact-grid img {
margin-bottom: 2em;
}
 .contact-grid p {
   font-family: 'Lato', sans-serif;
   font-weight: bold;
   text-align: center;
   width: 80%;
   padding-left: 10%;
 }

.contact-grid hr {
  border-top: 5px dotted black;
}

.contact-list {
  /*background: grey;*/
  font-family: 'Lato', sans-serif;
  min-width: 350px;
}

.contact-list i {
  font-size: 38px;
  padding-right: 30px;
}

/* Resume page CSS */
.resume-body {
  padding-top: 2em;
}

.resume-left-col {
  min-width: 380px;
}

.resume-right-col {
  background-color: #27221F;
  color: white;
  padding: 2em;
  border-radius: 15px;
}

.resume-avatar {
  width: 70%;
  border-radius: 15%;
}

@media screen and (max-width: 680px) {
  .resume-body {
    padding-top: 2em;
  }
  .resume-left-col {
    min-width: 50px;
  }
}

/* Exeperience CSS */
.jobDescription {
  width: 74%;
  text-align: justify;
}
